.g/*rig*/ {
  display: grid;
  grid-template-columns: repeat(6, calc(100vw / 6 - 1vw - 2px));
  grid-template-rows: repeat(20, calc(100vw / 6 - 1vw - 2px));
  grid-gap: 1vw;
}

.c/*ell*/ {
  border: 1px solid;
  display: grid; /* center contents */
}
.v/*alue*/ { 
  margin:auto; /* center contents */
}
.c:hover{
  border: 5px solid blue;
}